import React, { useState } from "react";
import styled from "styled-components";
import { Button } from "@material-ui/core";
import { db, auth } from "../firebase";
import firebase from "firebase";
import { useAuthState } from "react-firebase-hooks/auth";
import SendIcon from "@material-ui/icons/Send";

function ChatInput({ channelName, channelId, chatRef }) {
  const [input, setInput] = useState("");
  const [user] = useAuthState(auth);

  const sendMessage = (e) => {
    e.preventDefault(); //prevents refresh on button submit

    console.log(channelId);

    // channelId not being fetched

    //error: CollectionReference.doc() cannot be called with an empty path
    db.collection("rooms").doc(channelId).collection("messages").add({
      message: input,
      timestamp: firebase.firestore.FieldValue.serverTimestamp(),
      user: user.displayName,
      userImage: user.photoURL,
    });

    //scrolls to bottom when message is sent
    chatRef.current.scrollIntoView({
      behavior: "smooth",
    });

    setInput("");
  };
  return (
    <ChatInputContainer>
      <form>
        <input
          onChange={(e) => setInput(e.target.value)}
          value={input}
          placeholder={`Message #${channelName}`}
        />
        <Button type="submit" hidden onClick={sendMessage}>
          <SendIcon />
        </Button>
      </form>
    </ChatInputContainer>
  );
}

export default ChatInput;

const ChatInputContainer = styled.div`
  border-radius: 20px;

  > form {
    position: relative;
    display: flex;
    justify-content: center;
  }

  > form > input {
    position: fixed;
    bottom: 30px;
    width: 60%;
    border: 1px solid grey;
    border-radius: 3px;
    padding: 20px;
    outline: none;
  }

  > form > button {
    display: none !important;
    background-color: green;
    color: white;
    position: fixed;
    bottom: 40px;
    margin-left: 1100px;

    :hover {
      opacity: 0.8;
      background-color: green;
    }
  }
`;
